  .modal--responsive .modal__content {
    width: 100%; }
  .modal--fit-content .modal__content {
    width: auto; }
    @media (max-width: 767px) {
      .modal--fit-content .modal__content {
        width: 100%; } }
  .modal--no-scale {
    transform: none; }
  .modal--preload {
    transition: none; }
  .modal.is-active {
    transform: scale(1);
    visibility: visible;
    opacity: 1;
    pointer-events: auto; }

